.avenir__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.avenir__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.avenir__navbar-links_logo{
    margin-right: 2rem;
}

.avenir__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.avenir__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.avenir__navbar-links_container p,
.avenir__navbar-sign p,
.avenir__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.avenir__navbar-sign button,
.avenir__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    background: #ff4820;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.avenir__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.avenir_navbar-menu svg{
    cursor: pointer;
}

.avenir__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 230px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.avenir__navbar-menu_container{
    margin: 1rem 0;
}

.avenir__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .avenir__navbar-links_container{
        display: none;
    }
    .avenir__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .avenir__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px){
    .avenir__navbar{
        padding: 2rem;
    }
    .avenir__navbar-sign{
        display: none;
    }
    .avenir__navbar-menu_container{
        top: 20px;
    }
    .avenir__navbar-menu_container-links-sign{
        display: block;
    }
}